'use client';

import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import styles from '../../styles/register.module.css'; // Make sure this file exists
import '../../styles/globals.css'

export default function RegisterForm() {
    const [showPassword, setShowPassword] = useState(false);
    const [showConfirm, setShowConfirm] = useState(false);
    

    return (
        <div className={styles.container}>
            <header className={styles['hotel-container-header']}>
                <Image src="/icons/Menu Container.svg" alt="menu" width={120} height={50} />
            </header>

            <div className={styles['register-container']}>
                <div className={styles['register-card']}>

                    {/* Left image */}
                    <div className={styles['login-img']}>
                        <Image src="/images/login-image.png" alt="login visual" width={300} height={300} />
                    </div>

                    {/* Right form */}
                    <div className={styles['register-form-container']}>
                        <div className={styles['register-form']}>
                            <h3>Register</h3>

                            <div className={styles['register-fields']}>

                                <div className={`${styles['form-field']} ${styles['double']}`}>
                                    <div>
                                        <label htmlFor="first-name">First Name</label>
                                        <input id="first-name" type="text" placeholder="Vernost24" />
                                    </div>
                                    <div>
                                        <label htmlFor="last-name">Last Name</label>
                                        <input id="last-name" type="text" placeholder="Vernost24" />
                                    </div>
                                </div>

                                <div className={styles['form-field']}>
                                    <label htmlFor="email">Email</label>
                                    <input id="email" type="email" placeholder="Vernost24@gmail.com" />
                                </div>

                                <div className={`${styles['form-field']} ${styles['password-field']}`}>
                                    <label htmlFor="password">Password</label>
                                    <input
                                        id="password"
                                        type={showPassword ? 'text' : 'password'}
                                        placeholder="********"
                                    />
                                    <Image
                                        src="/icons/preview-close-one.svg"
                                        alt="toggle visibility"
                                        width={20}
                                        height={20}
                                        onClick={() => setShowPassword(prev => !prev)}
                                    />
                                </div>

                                <div className={`${styles['form-field']} ${styles['password-field']}`}>
                                    <label htmlFor="confirm-password">Confirm Password</label>
                                    <input
                                        id="confirm-password"
                                        type={showConfirm ? 'text' : 'password'}
                                        placeholder="********"
                                    />
                                    <Image
                                        src="/icons/preview-close-one.svg"
                                        alt="toggle visibility"
                                        width={20}
                                        height={20}
                                        onClick={() => setShowConfirm(prev => !prev)}
                                    />
                                </div>

                                <div className={`${styles['form-field']} ${styles['checkbox-field']}`}>
                                    <input type="checkbox" id="terms-checkbox" />
                                    <label htmlFor="terms-checkbox">
                                        I agree to all the Terms and Privacy Policies
                                    </label>
                                </div>

                                <button>Register Now</button>

                                <div className={styles['login-form-register-redirect']}>
                                    Already have an account?
                                    <Link href="/login"> Login</Link>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
}
